.vince_container {
  border-left: solid 1px black;
  border-right: solid 1px black;
}
@media (max-width: 900px) {
  .vince_container {
    position: relative;
  }
}

.logo {
  display: flex;
  padding-block-start: 65px;
  justify-content: flex-end;
  padding-inline: 80px;
}
@media (max-width: 900px) {
  .logo {
    position: absolute;
    top: 255px;
    right: 0px;
    padding-inline: 25px;
  }
}

.logo img {
  width: 150px;
  height: 75px;
}
@media (max-width: 900px) {
  .logo img {
    width: 70px;
    height: 35px;
  }
}

.vince_body {
  display: flex;
  align-items: center;
  justify-content: center;
  padding-inline: 80px;
  gap: 64px;
  padding-block-end: 135px;
}
@media (max-width: 900px) {
  .vince_body {
    flex-direction: column-reverse;
    padding-inline: 25px;
    padding-block-start: 65px;
    padding-block-end: 65px;
  }
}

.vince-img {
  border: solid 1px black;
  border-radius: 250px;
  width: 710px;
  height: 400px;
}
@media (max-width: 900px) {
  .vince-img {
    width: 330px;
    height: 185px;
  }
}

.vince_column-title h1 {
  font-size: 65px;
}
@media (max-width: 900px) {
  .vince_column-title h1 {
    font-size: 32px;
  }
}

.author {
  font-size: 16px;
  font-family: Nunito Sans, sans-serif;
  font-weight: 400;
  padding-block-start: 20px;
}

.author-text {
  max-width: 506px;
  font-size: 26px;
  font-family: Nunito Sans, sans-serif;
  font-weight: 400;
  padding-block-start: 25px;
  padding-block-end: 40px;
}
@media (max-width: 900px) {
  .author-text {
    font-size: 26px;
    padding-block-start: 20px;
    padding-block-end: 30px;
  }
}

.value_sucess {
  display: inline-block;
  padding-inline: 45px;
  padding-block: 25px;
  border: 1px solid black;
  box-shadow: 6px 6px 0 2px rgb(0, 0, 0);
}
@media (max-width: 900px) {
  .value_sucess {
    padding-inline: 35px;
    padding-block: 20px;
  }
}

.value_sucess p {
  font-size: 18px;
}
@media (max-width: 900px) {
  .value_sucess p {
    font-size: 14px;
  }
}

.value_sucess h1 {
  font-size: 80px;
}
@media (max-width: 900px) {
  .value_sucess h1 {
    font-size: 60px;
  }
}